@font-face {
  font-family: "PTMono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(PTMono-Regular), url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(RobotoCondensed-Regular), url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local(RobotoCondensed-Bold), url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local(Roboto-Medium), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

.body {
  margin: 0;
  position: relative;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  font-family: "PTMono", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: black;
  background-color: white; }

.container {
  margin: 0 auto;
  max-width: 1440px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  overflow: hidden; }

.link {
  text-decoration: none; }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.header {
  color: white;
  background-color: #1b2d37;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.header__wrapper {
  padding: 33px 15px;
  display: grid;
  grid-template-columns: 1fr auto; }

.header__logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 205px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__logo > span {
  margin-left: 5px;
  display: block;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px; }

.header__logo span > span {
  display: block;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

.header__nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__nav-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly; }

.header__nav-link {
  margin: 10px 40px;
  display: block;
  line-height: 18px;
  color: white; }

.header__nav-phone {
  white-space: nowrap; }

.header__nav-button {
  padding: 12px 0;
  min-width: 180px;
  text-align: center;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer; }
